import React, { FC, useState } from 'react';
import styled from 'styled-components';
import Lightbox from 'react-image-lightbox';
import Card from './Card';
import { ProjectItemTypes } from '../types/Common';
import parse from 'html-react-parser';

interface CardListsProps {
  itemLists: ProjectItemTypes[]
}

const CardLists: FC<CardListsProps> = ({ itemLists }) => {
  const [modalIsOpen, setModalOpen] = useState<boolean>(false)
  const [photoIndex, setPhotoIndex] = useState<number>(0)
  const [currentProject, setCurrentProject] = useState<ProjectItemTypes>()

  const viewProjectScreenshots = (item: ProjectItemTypes) => {
    setModalOpen(true)
    setCurrentProject(item)
  }
  return (
    <Container>
      {itemLists.map((item: ProjectItemTypes, index: number) => 
        <Card key={index}
          projectInfo={item} 
          viewProjectScreenshots={viewProjectScreenshots}
        />
      )}
      
      {modalIsOpen && currentProject && (
          <Lightbox
            mainSrc={currentProject.screenshots[photoIndex]}
            nextSrc={currentProject.screenshots[(photoIndex + 1) % currentProject.screenshots.length]}
            prevSrc={currentProject.screenshots[(photoIndex + currentProject.screenshots.length - 1) % currentProject.screenshots.length]}
            onCloseRequest={() => setModalOpen(false)}
            onMovePrevRequest={() =>
              setPhotoIndex((photoIndex + currentProject.screenshots.length - 1) % currentProject.screenshots.length,
              )
            }
            onMoveNextRequest={() =>
              setPhotoIndex((photoIndex + 1) % currentProject.screenshots.length)
            }
            imageTitle={parse(currentProject.title)}
            imageCaption={currentProject.description}
          />
        )}
    </Container>
  );
}


const Container = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
`
export default CardLists;
