import React, { FC } from 'react';
import styled from 'styled-components';
import { ProjectItemTypes } from '../types/Common';
import parse from 'html-react-parser';

interface CardTypesProps {
  projectInfo: ProjectItemTypes
  viewProjectScreenshots: (item: ProjectItemTypes) => void
}
const Card: FC<CardTypesProps> = ({ projectInfo, viewProjectScreenshots }) => {
  return (
    <Figure onClick={() => viewProjectScreenshots(projectInfo)}>
      <FigureImage src={projectInfo.screenshots[0]} alt="sample40"/>
      <FigureCaption>
        <FigureH2>{parse(projectInfo.title)}</FigureH2>
        <FigureText>{projectInfo.description}</FigureText>
      </FigureCaption>			
    </Figure>
  );
}


const Figure = styled.figure`
  font-family: 'Raleway', Arial, sans-serif;
  color: #fff;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin: 10px;
  border-radius: 20px;
  min-width: 220px;
  max-height: 220px;
  width: 100%;
  background: #000000;
  text-align: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  &:hover img {
    opacity: 0.35;
  }
`

const FigureImage = styled.img`
  opacity: 1;
  width: 100%;
  -webkit-transition: opacity 0.35s;
  transition: opacity 0.35s;
`
const FigureH2 = styled.h2`
  margin: 0 0 5px;
  font-weight: 200;
  font-size: 16px;
  text-transform: uppercase;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
  opacity: 0;
  span {
    font-size: 26px;
    font-weight: 800;
  }
`
const FigureText = styled.p`
opacity: 0;
  font-weight: 200	;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
`
const FigureCaption = styled.figcaption`
  font-family: 'Raleway', Arial, sans-serif;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 3em 3em;
  width: 100%;
  height: 100%;
  &::before {
    position: absolute;
    top: 50%;
    right: 30px;
    bottom: 50%;
    left: 30px;
    border-top: 1px solid rgba(255, 255, 255, 0.8);
    border-bottom: 1px solid rgba(255, 255, 255, 0.8);
    content: '';
    opacity: 0;
    background-color: #ffffff;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    -webkit-transition-delay: 0.6s;
    transition-delay: 0.6s;
  }
  &:hover ${FigureH2} {
    opacity: 1;
  }
  &:hover ${FigureImage} {
    opacity: 0.35;
  }
  &:hover ${FigureText} {
    opacity: 0.9;
    -webkit-transition-delay: 0.4s;
    transition-delay: 0.4s;
  }
  &:hover {
    &::before {
      background: rgba(255, 255, 255, 0);
      top: 30px;
      bottom: 30px;
      opacity: 1;
      -webkit-transition-delay: 0s;
      transition-delay: 0s;
    }
  }
`
export default Card;
